<template>
  <div id="app" class="wrapper">
    <div class="loginBox">
        <div class="loginBoxCenter">
          <p><label for="username">用户名：</label></p>
          <p><input type="text" id="text" name="text" class="loginInput" autofocus="autofocus" required="required" autocomplete="off" placeholder="请输入邮箱/手机号" value="" /></p>
          <p><label for="password">密码：</label></p>
          <p><input type="password" id="password" name="password" class="loginInput" required="required" placeholder="请输入密码" value="" /></p>
          <p><a class="forgetLink" href="#">忘记密码?</a></p>
          <input id="remember" type="checkbox" name="remember" />
          <label for="remember">记住登录状态</label>
        </div>
        <div class="loginBoxButtons">
          <button class="loginBtn" @click="doLogin()">登录</button>
          <div>
            <a href="register.html">新用户注册</a>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'login',
  data() { 
    return {
      loginData: ''
    }
  },
  methods: {
    doLogin() {
      APIHTTP.request('user/login', null, 0, (res)=> {
        this.loginData = JSON.parse(res.data)
        console.log(this.loginData)
        if (this.loginData.loginStatus) {
          window.location.href = 'home.html'
        } else {
          window.location.href = 'error.html'
        }
      })
    }
  }
}
</script>
<style>
  html {
    background-color: #B5DEF2;
  }
  /* .wrapper {
    margin: 0 0 0 auto;
    width: 884px;
  } */
  .loginBox {
      background-color: #F0F4F6;
      /*上divcolor*/
      border: 1px solid #BfD6E1;
      border-radius: 5px;
      color: #444;
      font: 14px 'Microsoft YaHei', '微软雅黑';
      margin: 0 auto;
      width: 388px
  }
  
  .loginBox .loginBoxCenter {
      border-bottom: 1px solid #DDE0E8;
      padding: 24px;
  }
  
  .loginBox .loginBoxCenter p {
      margin-bottom: 10px
  }
  
  .loginBox .loginBoxButtons {
      /*background-color: #F0F4F6;*/
      /*下divcolor*/
      border-top: 0px solid #FFF;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      line-height: 28px;
      overflow: hidden;
      padding: 20px 24px;
      vertical-align: center;
      filter: alpha(Opacity=80);
      -moz-opacity: 0.5;
      opacity: 0.5;
  }
  
  .loginBox .loginInput {
      border: 1px solid #D2D9dC;
      border-radius: 2px;
      color: #444;
      font: 12px 'Microsoft YaHei', '微软雅黑';
      padding: 8px 14px;
      margin-bottom: 8px;
      width: 310px;
  }
  
  .loginBox .loginInput:FOCUS {
      border: 1px solid #B7D4EA;
      box-shadow: 0 0 8px #B7D4EA;
  }
  
  .loginBox .loginBtn {
      background-image: -moz-linear-gradient(to bottom, blue, #85CFEE);
      border: 1px solid #98CCE7;
      border-radius: 20px;
      box-shadow: inset rgba(255, 255, 255, 0.6) 0 1px 1px, rgba(0, 0, 0, 0.1) 0 1px 1px;
      color: #444;
      /*登录*/
      cursor: pointer;
      float: right;
      font: bold 13px Arial;
      padding: 10px 50px;
  }
  
  .loginBox .loginBtn:HOVER {
      background-image: -moz-linear-gradient(to top, blue, #85CFEE);
  }
  
  .loginBox a.forgetLink {
      color: #ABABAB;
      cursor: pointer;
      float: right;
      font: 11px/20px Arial;
      text-decoration: none;
      vertical-align: middle;
      /*忘记密码*/
  }
  
  .loginBox a.forgetLink:HOVER {
      color: #000000;
      text-decoration: none;
      /*忘记密码*/
  }
  
  .loginBox input#remember {
      vertical-align: middle;
  }
  
  .loginBox label[for="remember"] {
      font: 11px Arial;
  }
</style>


